<template>
    <div class="detail">
        <van-nav-bar
        title="商品详情"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        fixed
        class="buy"
        />
        <div class="img">
            <img src="https://a4.vimage1.com/upload/merchandise/pdc/544/548/464510208477548544/0/880555-001-5_218x274_70.jpg" alt="">
        </div>
        <div class="price">
            <h3>￥1342.58</h3>
            <span>耐克WEs3242男款超级跑鞋</span>
        </div>
        <van-goods-action>
        <van-goods-action-icon
            icon="chat-o"
            text="客服"
            @click="onClickIcon"
        />
        <van-goods-action-icon
            icon="cart-o"
            text="购物车"
            @click="onClickIcon"
        />
        <van-goods-action-button
            type="warning"
            text="加入购物车"
            @click="onClickButton"
        />
        <van-goods-action-button
            type="danger"
            text="立即购买"
            @click="onClickButton"
        />
        </van-goods-action>
    </div>
</template>
<script>
import Vue from 'vue';
import { NavBar } from 'vant';
Vue.use(NavBar);
import {
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton
} from 'vant';

Vue
  .use(GoodsAction)
  .use(GoodsActionIcon)
  .use(GoodsActionButton);
export default {
  name:'detail',  
  methods: {
    onClickLeft() {
      this.$router.go(-1)
    },
    onClickIcon() {
      Toast('点击图标');
    },
    onClickButton() {
      Toast('点击按钮');
    }
  },
  mounted(){
      let id=this.$route.params.aid
      let dataName=this.$route.query.dataName
      axios({
          url:`/api/${dataName}/${id}`,
          params:{_page:1,_limit:10}
      }).then(
          res=>this.detail=res.data.data
      )
  } 
}
</script>
<style scoped>
.van-goods-action{margin-bottom:500px; }
.price h3{color:purple;text-align: left;font: 700 20px/2 "";height:40px;}
.price{text-align: left;}
</style>

